<template>
  <div>
    <div class="info_info">
      <div class="info_base">
        <section class="person_info_base">
          <span>姓名: <strong>{{name}}</strong></span>
          <span>电话: <strong>{{tel}}</strong></span>
          <span>类型: <strong>{{room_type}}</strong></span>
          <span>床的尺寸: <strong>{{bed_type}}</strong></span>
        </section>
        <div class="address">
           <span style="padding: 0 0.2rem;display:inline-block;">所属门店: <strong>{{stores_name}}</strong></span>
          <span style="padding: 0 0.3rem;display:inline-block;">地址: <strong>{{address}}</strong></span>
        </div>
      </div>
      <div class="goods_list">
        <section class="list_header">
          <span class="item">订单号: <strong>{{order}}</strong></span>
          <span class="item">订单状态: <strong>{{state}}</strong></span>
        </section>
      </div>
      <ul class="goods">
        <li style="display: flex;justify-content: space-between;align-items: center">
          <div class="left">
            <img :src="pic" alt="" class="photo_img"/>
            <p class="left_info">
              <span>{{name}}</span>
              <span><strong>{{num}}天</strong> <strong style="display: inline-block;margin-left: 0.1rem">{{spec_value}}</strong></span>
              <span>¥{{total_price}}</span>
            </p>
          </div>
        </li>
      </ul>
      <div>
        <div class="fahuo" v-if="state === '已付款' ">
          <Button type="info" @click.native="send(state)">入住</Button>
          <Button type="warning" @click.native="refund">退款</Button>
        </div>
      </li>
    </ul>
        <div class="fahuo">
          <Button type="info" @click.native="send(state)"  v-if = "state === '已入住'">已完成</Button>
        </div>
        <div class="fahuo">
          <Button type="info" @click.native="refund(state)"  v-if = "state === '退款中'">退款</Button>
        </div>
         <div class="fahuo">
          <p style="font-size: 16px; color: red;"  v-if = "state === '已退款'">退款中...</p>
        </div>
        <div class="fahuo">
          <p style="font-size: 16px; color: red;"  v-if = "state === '已完成'">订单已完成</p>
        </div>
    </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        name: "",
        tel: '',
        email: '',
        id: "",
        address: "",
        order: "",
        state: "",
        order_id: '',
        total_price:'',
        num:'',
        spec_value:'',
        room_type:'',
        goodsOrder: '',
        modal1: false,
        model: '',
        pic:'',
        bed_type:'',
        stores_name:'',
      }
    },
    methods: {
      ok() {
        this.handelOrder(2)
      },
      cancel() {

      },
      //    封装
      handelOrder(state, order) {
        this.post("custom/Hotel/update_order_state", {
          appid: this.$route.params.appid,
          state: state,
          id: this.order_id
        }).then(res => {
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "成功",
              content: res.data.msg_test
            });
          } else {
             this.$Modal.error({
              title: "失败",
              content: res.data.msg_test
            });
          }
        })
      },
      send(state) {
        state==="已付款"?this.handelOrder(3):this.handelOrder(5)
      },
//    退款
      refund(item) {
        this.post("custom/Hotel/refunds", {
          appid: this.$route.params.appid,
          id: this.order_id
        }).then(res => {
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "成功",
              content: res.data.msg_test
            });
          } else {
             this.$Modal.error({
              title: "失败",
              content: res.data.msg_test
            });
          }
        })
//      this.handelOrder(4)
      }
    },
    activated() {
      this.$store.commit('setStatus', false);
      this.order_id = this.$route.params.id;
      this.post("custom/Hotel/get_order_detail", {
        id: this.order_id
      }).then(res => {
        if(res.data.code === 10000) {
          this.name = res.data.data.username;
          this.tel = res.data.data.user_tel;
          this.email = res.data.data.mail;
          this.room_type = res.data.data.room_type;
          this.address = res.data.data.address;
          this.order = res.data.data.order_sn;
          this.state = res.data.data.state;
          this.num = res.data.data.num;
          this.bed_type = res.data.data.bed_type;
          this.total_price = res.data.data.total_price;
          this.stores_name =res.data.data.stores_name;
          this.pic = "http://weapp.xiguawenhua.com/" + res.data.data.room_photo
          if(this.state === 1) {
            this.state = "已付款"
          } else if(this.state === 2) {
            this.state = "已退款"
          } else if(this.state === 3) {
            this.state = "已入住"
          } else if(this.state === 4) {
            this.state = "退款中"
          }else if(this.state === 0){
            this.state = "未付款"
          }else if(this.state === 5){
            this.state = "已完成"
          }
        } else {
        }
      })
    }
  }
</script>
<style scoped>
  .top {
    padding: 0.1rem 0;
    border-bottom: 1px solid #ebecee
  }

  header {
    display: flex;
    align-items: center;
  }

  header>span {
    font-size: 0.18rem;
    display: inline-block;
    margin-left: 0.1rem
  }

  p {
    padding: 0.1rem 0;
    font-size: 0.14rem;
    letter-spacing: 0.01rem
  }

  .info {
    padding: 0.1rem 0;
  }

  .person_info_base {
    background-color: white;
    padding: 0.2rem 0.2rem;
    border-bottom: 1px solid #EEEEEE;
  }

  .person_info_base>span {
    display: inline-block;
    padding: 0 1rem;
  }

  .person_info_base>span>strong {
    display: inline-block;
    margin-left: 0.05rem
  }

  .person_info_base>span:nth-child(1) {
    display: inline-block;
    padding: 0 0.2rem;
  }

  .address {
    padding: 0.2rem 0.2rem;
    margin-top: 0.01rem
  }

  .goods_list {
    margin-top: 0.2rem
  }

  .list_header {
    padding: 0.2rem 0.2rem;
    background-color: #F9FAFB;
    display: flex;
    justify-content: space-between
  }

  .item {
    color: #4E4E4E;
    padding: 0 0.2rem
  }

  .goods {
    padding: 0.2rem 0.2rem;
    border: 1px solid #f3f3f3;
    background: white;
  }

  .photo_img {
    width: 0.88rem;
    max-height: 0.8rem;
    margin: 0 0.2rem
  }

  .left {
    display: flex;
    justify-content: flex-start;
    align-items: center
  }

  .left_info {
    display: flex;
    flex-direction: column;
    margin-left: 0.1rem
  }

  .left_info>span {
    display: inline-block;
    padding: 0.02rem 0;
  }

  .right {
    padding: 0 0.2rem
  }

  .fahuo {
    margin-top: 20px;
    float: right;
    margin-right: 40px;
  }
  .info_base{
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>



